body, body > div, body > div > div {
    margin: 0;
    height: 100vh;
    width: 100%;
    overflow-x: hidden;
    font-family: 'Inter', sans-serif;
}
h1, h2 {
    font-family: 'Oswald', sans-serif;
}
a {
    color: unset;
}
header {
    background: #00000040;
    border-bottom: 2px solid #ffffff40;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
select > option {
    text-align: center;
}
button {
    background: transparent;
    border: 0;
    cursor: pointer;
}
footer {
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 99;
    margin: 2% 0 0 0;
    position: fixed;
    bottom: 0;
    width: 100%;
}
footer > span {
    display: block;
    margin: 3px;
    text-align: center;
}
pre {
    font-family: 'Source Code Pro', monospace;
    margin: 0;
}
main {
    display: flex;
}
::-webkit-scrollbar {
    width: 1vh;
    height: 1vh;
    background: #333;
    cursor: pointer;
}
/* Track */
::-webkit-scrollbar-track {
    background: #00000000;
}
/* Handle */
::-webkit-scrollbar-thumb {
    background: #111;
    border-radius: 5vh;
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: black;
}
aria-label {
    display: none;
    background: #222;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px;
    position: absolute;
    z-index: 98;
    bottom: calc(5vh + 5px);
    left: calc(50% - 3.5rem);
    opacity: 0;
    width: 7rem;
    height: auto;
    transition: opacity 0.5s;
}
aria-label::after {
    content: "";
    display: flex;
    width: 0;
    height: 0;
    position: absolute;
    bottom: -10px;
    left: calc(50% - 10px);
    border-left: 10px solid transparent;
    border-top: 10px solid #222;
    border-right: 10px solid transparent;
}
*:hover > aria-label {
    opacity: 1;
    display: flex;
}

.fade-up {
    transform: translateY(25%);
    opacity: 0;
    transition: all 0.5s ease;
}
.fade-up.active, .fade-up:nth-child(1) {
    transform: translateY(0);
    opacity: 1;
}
.fade-down {
    transform: translateY(-25%);
    opacity: 0;
    transition: all 0.5s ease;
}
.fade-down.active {
    transform: translateY(0);
    opacity: 1;
}